/* settings button */
.SettingsOption_tglflip__a-can {
    display: none;
}

.SettingsOption_tglflip__a-can + .SettingsOption_tglbtn__3paKw {
    cursor: pointer;
	width: 50px;
	padding: 2px;
	transition: all 0.2s ease;
	font-family: sans-serif;
    perspective: 100px;
    margin: auto;
    display: flex;
    align-items: center;
}

.SettingsOption_tglflip__a-can + .SettingsOption_tglbtn__3paKw:after, .SettingsOption_tglflip__a-can + .SettingsOption_tglbtn__3paKw:before {
	display: inline-block;
	transition: all 0.4s ease;
	width: 40px;
	text-align: center;
	position: absolute;
	line-height: 2em;
	font-size: 13px;
	color: black;
	font-weight: bold;
	padding: 1px;
	position: absolute;
	top: 0;
	left: 0;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
	border-radius: 4px;
}

.SettingsOption_tglflip__a-can + .SettingsOption_tglbtn__3paKw:after {
	content: attr(data-tg-on);
    background: #42e86b;
    color: black;
    font-weight: bold;
	transform: rotateY(-180deg);
}

.SettingsOption_tglflip__a-can + .SettingsOption_tglbtn__3paKw:before {
	background: #ff6e6e;
	content: attr(data-tg-off);
}

.SettingsOption_tglflip__a-can + .SettingsOption_tglbtn__3paKw:active:before {
	transform: rotateY(-20deg);
}

.SettingsOption_tglflip__a-can:checked + .SettingsOption_tglbtn__3paKw:before {
	transform: rotateY(180deg);
}

.SettingsOption_tglflip__a-can:checked + .SettingsOption_tglbtn__3paKw:after {
	transform: rotateY(0);
	left: 0;
}

.SettingsOption_tglflip__a-can:checked + .SettingsOption_tglbtn__3paKw:active:after {
	transform: rotateY(20deg);
}
